/* Base styles */
.App {
  text-align: center;
  background-color: #f0f0f0;
  min-height: 100vh;
  color: #333;
  display: flex;
  flex-direction: column;
}

.App-header {
  background-color: #282c34;
  padding: 1rem;
  color: white;
}

.chat-container {
  flex: 1;
  padding: 1rem;
  overflow-y: auto;
  max-height: 60vh;
}

.message {
  margin: 0.5rem 0;
  padding: 0.5rem;
  border-radius: 5px;
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.message.user {
  background-color: #e0f7fa;
}

.message.bot {
  background-color: #ffe0b2;
}

.input-container {
  display: flex;
  padding: 1rem;
  justify-content: center;
}

.input-container input {
  width: 60%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.input-container button {
  margin-left: 0.5rem;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
}

.sleepy-message {
  padding: 1rem;
  font-style: italic;
}

/* Cave mode styles for dark UI */
.cave-mode {
  background-color: #121212;
  color: #e0e0e0;
}

.cave-mode .App-header {
  background-color: #1f1f1f;
}

.cave-mode .message.user {
  background-color: #263238;
}

.cave-mode .message.bot {
  background-color: #37474f;
}

.cave-mode .input-container input {
  background-color: #263238;
  color: #e0e0e0;
  border: 1px solid #555;
}

.cave-mode .input-container button {
  background-color: #546e7a;
}
